import React, { useEffect, useState } from 'react';
import {Link} from 'react-router-dom';
import {useHttp} from './Hooks/http.hook';
import {makeRepoList} from './Repos';


const User = ({user}) => {
  const [repos, setRepos] = useState([]);
  const {loading, error, request, clearError} = useHttp();

  useEffect(async () => {
    try {
        const data = await request(`${user.repos_url}?per_page=3`)
        setRepos(data);
    } catch(err) {
        console.log(err)
    }
  }, [])

  makeRepoList(repos);
  
  return (
    <li 
      className="users-wrapper__item"
    >
      <div className="users-wrapper__item-info">
        <img 
              className="users-wrapper__item-info-img" 
              src={user.avatar_url} 
              alt="user_img"
        />
        <span className="users-wrapper__item-info-type">
          Type: {user.type} â€” 
        </span>

        <Link to={`/${user.login}`}>&nbsp;{user.login}</Link>
      </div>

      <div className="repos">
        <div className="text">Repositories:</div>
        <div className="repos-linksWrap">
          {
              loading ? null :
              repos.length === 0 ? null :
              makeRepoList(repos).map(item => item)
          }
        </div>
      </div>
    </li>
  )
}

export default User;