import React, { useEffect, useState } from 'react';
import {useHttp} from './Hooks/http.hook';
import {Link} from 'react-router-dom';
import User from './User';

// Font Awesome
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faThLarge, faList } from '@fortawesome/free-solid-svg-icons';
import { useHistory } from 'react-router';

const gridIco = <FontAwesomeIcon icon={faThLarge} />
const listIco = <FontAwesomeIcon icon={faList} />

const API_URL = 'https://api.github.com/search/users?q=followers:>=1000&per_page=52';

const MainPage = () => {
    const [popularUsers, setPopularUsers] = useState([]);
    const [viewType, setViewType] = useState("list");
    const {loading, error, request, clearError} = useHttp();
    const [searchValue, setSearchValue] = useState("");
    const history = useHistory();

    useEffect(async () => {
      try {
          const data = await request(API_URL)
          setPopularUsers(data.items);
      } catch(err) {
          console.log(err)
      }
    }, [])

    const handleClickViewType = () => {
      if (viewType === "list") {
        setViewType("grid");
      }
      
      if (viewType === "grid") {
        setViewType("list");
      }
    }
  
    const handleChangeInput = event => {
      setSearchValue(event.target.value)
    }

    const keyPress = event => {
      if (event.key === 'Enter') {
        history.push(`/${searchValue}`)
        const usersInLC = Object.keys(localStorage).filter(key =>  key.includes('user'));

        if (usersInLC.length < 3) {
          localStorage.setItem(`user${localStorage.length}`, JSON.stringify(searchValue));
        } else {
          localStorage.setItem(`user3`, JSON.stringify(searchValue));
        }
      }
    }

    const searchHistory = () => {
      let historyArr = [];
      for (let i = 0; i < localStorage.length; i++) {
        historyArr.push(getSearchHistoryItem(i))
      }

      return historyArr;
    }

    const inputFocus = () => {
      if (localStorage.length > 0) {
        const element = document.querySelector('#searchHistory');
        element.style.display = 'flex';
      }
    }
    const inputUnFocus = () => {
      const element = document.querySelector('#searchHistory');
      setTimeout(() => element.style.display = 'none', 300);
    }

    const getSearchHistoryItem = (index) => {
      const user = localStorage[`user${index}`]?.replace(/"/g, "");
      return (
        <div 
          key={localStorage[`user${index}`]} 
          className="header-search__history-item"
        >
          <Link to={`/${user}`}>
            {user}
          </Link>
        </div>
      )
    }

    return (
      <div className="mainPage mainPage-outer">
        <div className="header header-outer">
          <div className="header-inner container">
            <div className="header-title">
              Most popular users in GitHub!
            </div>
  
            <div className="header-search">
              <input 
                className="header-search__input"
                type="text" 
                value={searchValue}
                placeholder="Search user..."
                onChange={handleChangeInput}
                onKeyPress={keyPress}
                onFocus={inputFocus}
                onBlur={inputUnFocus}
              />
              <div id="searchHistory" className="header-search__history">
                {
                  localStorage.length > 0 ? searchHistory() : null
                }
              </div>
            </div>
  
            <div className="header-viewType" onClick={handleClickViewType}>
              {viewType === "list" ? 
              <> <span className="text">List</span> {listIco}</> : 
              <> <span className="text">Grid</span> {gridIco}</>}
            </div>
          </div>
        </div>
  
        <div className="mainPage-inner container">
          <ul id="usersWrapId" className={`${viewType} users-wrapper`}>
            {
              loading ? <div className="loading">Loading...</div>:
              popularUsers.map(item => <User key={item.id} user={item}/>)
            }
          </ul>
        </div>
      </div>
    );
}

export default MainPage;